export default {
  data() {
    return {
      title: '',                         // 顶部标题
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#ffffff',   // 以上轮播图信息
      timeList: [],                      // 时间
      selectIndex: 0,                    // 选中的时间
      goodList: [
        {
          title: 'Sony/索尼 MDR-ZX310头戴式监听重低音耳机Sony/索尼 MDR-ZX310头戴式监听重低音耳机',
          leaver_sum: 40,
          total: 100,
          discount: '4.6折',
          newPrice: 300,
          oldPrice: 4000,
          code: 1,
          percent: 40
        }]
    }
  },
  onLoad(option) {
    console.log('分享文章详情页接受到的参数',option.origin)
    this.title = option.origin
    uni.setNavigationBarTitle({
      title: option.origin
    })
  },
  onShow() {
    // 获取秒杀或者限时购活动列表
    this.getSeckillCategorys()
  },
  watch: {
    selectIndex(val, oldval) {
      // 点击顶部时间动态获取秒杀商品
      this.getgoodsbycategoryid(this.timeList[val].category_id)
    }
  },
  methods: {
    // 获取秒杀活动列表
    getSeckillCategorys() {
      let url = this.$api.seckill_categorys
      if(this.title === '限时购') {
        url = this.$api.flashsale_categorys
      }
      this.$http({
        url: url,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.timeList = res.data.list
            
            // 通过第一个活动ID获取秒杀商品
            this.getgoodsbycategoryid(this.timeList[0].category_id)
            
          } else if(res.code === 0 || res.code === -1 & res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '秒杀活动列表加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 通过秒杀活动ID获取秒杀商品列表
    getgoodsbycategoryid(id) {
      let url = this.$api.seckill_goodsbycategoryid
      if(this.title === '限时购') {
        url = this.$api.flashsale_goodsbycategoryid
      }
      this.$http({
        url: url,
        data: {
          category_id: id
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            console.log('时间', res.data.list)
            this.goodList = res.data.list
          } else if(res.code === 0 || res.code === -1 & res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '秒杀商品列表加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    
    selectTime(item, index) {
      this.selectIndex = index
    },
    // 去购买或者进入详情页
    goDetail(item) {
      console.log('点击了商品', item)
      // uni.navigateTo({
      //   url: '../components/goodDetail?info=' + info + '&panic=true'
      // })
    }
  },
}
